export const Juro = () => {
  const [step, setStep] = useState(-1)
  const [last, setLast] = useState('')
  const [countries, setCountries] = useState(JSON.parse(JSON.stringify((initCountries[0]))))
  const [steps, setSteps] = useState(JSON.parse(JSON.stringify(Object.assign(initSteps[0]))))

  const handleChange = () => {
    if (step >= steps.length-1) return
    var newStep = step+1
    countries[steps[newStep].country] += steps[newStep].points
    setStep(newStep)
    setLast(steps[newStep].country)
  }

  const handleDataChange = (evening) => {
    setCountries(JSON.parse(JSON.stringify((initCountries[evening]))))
    setSteps(JSON.parse(JSON.stringify(Object.assign(initSteps[evening]))))
    setStep(-1)
    setLast('')
  }

  return (
    <>
      <AppBar position="static">
        <Toolbar>
          <Button color="inherit" onClick={() => handleDataChange(0)}>
            Fyrra undankvöld
          </Button>
          <Button color="inherit" onClick={() => handleDataChange(1)}>
            Seinna undankvöld
          </Button>
        </Toolbar>
      </AppBar>
      <Container>
        <Card>
          <CardContent>
            <Grid container>
              <Grid container item xs direction="column" alignItems="center">
                <Button align="center" variant="outlined" onClick={handleChange}>Áfram með stuðið!</Button>
                <Grid item>
                {steps[step]?.player ? (<img src={`imgs\\${steps[step]?.player}.jpg`} height="300px" maxWidth="80%" />) : null}
                </Grid>
                <Typography align="center" variant="h4">{steps[step]?.player}</Typography>
                <Typography align="center" variant="h3">{steps[step]?.country}</Typography>
                <Typography align="center" variant="h3">{steps[step]?.points}</Typography>
              </Grid>
              <Grid item xs>
                <Table size="small">
                  <TableHead>
                    <TableRow>
                      <TableCell>Sæti</TableCell>
                      <TableCell>Land</TableCell>
                      <TableCell>Stig</TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {Object.keys(countries).map(key => { return { label: key, points: countries[key] }}).sort((a, b) => a.points < b.points).map((country, i) => {
                      return (<TableRow key={country.label}>
                        <TableCell>{i+1}</TableCell>
                        <TableCell>{last === country.label ? (<Typography color="primary">{country.label}</Typography>) : country.label}</TableCell>
                        <TableCell>{country.points}</TableCell>
                      </TableRow>)
                    })
                    }
                  </TableBody>
                </Table>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Container>
    </>
  )
}